import React, { Component } from 'react';
import Zoom from 'react-reveal/Zoom';


class Mainpage extends Component {
    state = { 
    assetDetails: null,       
    };

    async componentDidMount() {
        this.setState({
            NumberOfAssets: 10,
            assets: this.props.assets,
            poolList: '',
            
        });
        
    };

    render() { 
        let poolOutput = '';
        if (typeof(this.props.pools) != 'undefined'){
            poolOutput = this.props.pools.map((pool,index) =>

                <tr key={index}>
                    <td>{pool[0]}</td>
                    <td className="text-left">{pool[3]}</td>
                    <td className="text-right">{this.props.outputNumber(pool[10],2)}</td>
                    <td className="text-right">{this.props.outputNumber(pool[9]/1000,0)}</td>
                    <td className="text-right">{this.props.outputNumber(pool[2]/1e18,0)}</td>                                            
                    <td className="text-right">{this.props.outputNumber(pool[2] * parseInt(pool[4]) /parseInt(pool[5]) /1e18,0)}</td>
                    <td className="text-right">{((pool[6]*100).toFixed(0)).toLocaleString()}%</td>
                </tr>
            ); 
        }
        else {
            <div className="spinner-border text-accent m-3" role="status"></div>
        }  
        return ( 

                <Zoom>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col w-100 container bg-accent m-3 rounded border border-accent">

                            <div className="row">
                               
                                <div className="col-md-3 h-100 container bg-accent  text-dark p-2 rounded">
                                    <div className="container p-4 text-dark bg-light rounded border border-dark">
                                        
                                            Current IPT Price: <b>{this.props.outputNumber(parseFloat(this.props.INTPrice),3)}</b>
                                        
                                    </div>
                                </div>

                                <div className="col-md-3 h-100 container bg-accent text-dark p-2 rounded">
                                    <div className="container p-4 text-dark bg-light rounded border border-dark">
                                        
                                            Current Number of Assets: <b>{typeof(this.props.assets)!== 'undefined' ? this.props.assets.length*2+1:0}</b>
                                        
                                    </div>
                                </div>
                                <div className="col-md-3 h-100 container bg-accent text-dark p-2 rounded border border-accent">
                                    <div className="container p-4 text-dark bg-light rounded border border-dark">
                                        
                                            Total value locked: <b>USDC {typeof(this.props.totalLockedValue)!== 'undefined' ? this.props.outputNumber(this.props.totalLockedValue/1e24,2):0}m</b>
                                        
                                    </div>
                                </div>  
                                <div className="col-md-3 h-100container bg-accent text-dark p-2 rounded border border-accent">
                                    <div className="container p-4 text-dark bg-light rounded border border-dark">
                                        
                                            Market Cap IPT: <b>USDC {typeof(this.props.assets)!== 'undefined' ? this.props.outputNumber(this.props.INTMarketCap/1e6,1):0}m</b>
                                        
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                    
                    <div className="row">
                       
                        <div className="col w-100 container bg-accent p-2 m-3 rounded border border-accent">
                            
                                <div className="bg-light text-dark p-3 rounded border border-dark">
                                    <h5>ISSUAA Assets and pools:</h5>
                                    <br></br>
                                    <table className="table w-100 d-block d-md-table">
                                    <thead className="text-dark">
                                        <th scope="col">Symbol</th>
                                        <th className="text-left" scope="col">Name of ISSUAA Asset / Underlying</th>
                                        <th className="text-right" scope="col">Price</th>
                                        <th className="text-right" scope="col">Upper Limit</th>
                                        <th className="text-right" scope="col">Pool liquidity (USDC)</th>                                            
                                        <th className="text-right" scope="col">Your stake (USDC)</th>
                                        <th className="text-right" scope="col">APR</th>
                                        
                                    </thead>
                                
                                    <tbody>
                                        
                                        {poolOutput}
                                    </tbody>
                                </table>
                                </div>
                            
                        </div>

                        
                    </div>


                </div>
                </Zoom>
             


            
         );
    }
}
 
export default Mainpage;