
import {Component} from 'react';
import {ToggleButtonGroup, ButtonGroup, ToggleButton, Button} from "react-bootstrap";
import logo from '../img/ISSUAAbeastmode_long.png';
import logo_alpha from '../img/ISSUAAbeastmode_alpha.png';

class Navbar extends Component{


  
  constructor(props) {
    super(props);
    this.state = {  
      account: '',
      loading: true,
    }
  }

  render(){
    return (
        <div className="container-fluid">
            <nav className="navbar navbar-expand-xl navbar-dark bg-dark border-bottom border-light">
                <a className="navbar-brand" href="#" onClick={() =>this.props.changeView('mainpage')}>
                  <img src={logo_alpha} alt="Logo" height="70"/>
                </a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">   
                            <a className="nav-link" href="#" onClick={() =>this.props.changeView('portfolio')}>Portfolio</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#" onClick={() =>this.props.changeView('factory')}>
                                Mint Assets
                            </a>
                        </li>
                        <li className="nav-item">   
                            <a className="nav-link" href="#" onClick={() =>this.props.changeView('pool')}>Pool</a>
                        </li>
                        <li className="nav-item">   
                            <a className="nav-link" href="#" onClick={() =>this.props.changeView('tradeassets')}>Trade Assets</a>
                        </li>
                        <li className="nav-item">   
                            <a className="nav-link" href="#" onClick={() =>this.props.changeView('burnassets')}>Redeem Assets</a>
                        </li>
                        <li className="nav-item">   
                            <a className="nav-link" href="#" onClick={() =>this.props.changeView('easyFarm')}>Easy Farm</a>
                        </li>
                        <li className="nav-item">   
                            <a className="nav-link" href="#" onClick={() =>this.props.changeView('governance')}>Governance</a>
                        </li>
                        <li className="nav-item">   
                            <a className="nav-link" href="https://issuaa.gitbook.io/issuaa/" target="_blank">Docs</a>
                        </li>
                        <li className="nav-item">   
                            <a className="nav-link" href="https://medium.com/@issuaa" target="_blank">Blog</a>
                        </li>
                        
                    </ul>
                    
                    {this.props.chainName == 'MaticTestnet' ? 
                        <Button size="sm" variant="accent" className="mr-1 text-dark" onClick={() =>this.props.changeView('test')}>Test MATIC / USD</Button>
                        :
                        ''
                    }
                    {this.props.chainName == 'MaticTestnet' ? 
                        <ToggleButtonGroup variant= "accent" name="version" type="radio" className="mr-1" size="sm" defaultValue={this.props.version}>
                            <ToggleButton variant= "accent" id="v1" value="v1" onClick={() =>this.props.switchVersion('v1')}>V1</ToggleButton> 
                            <ToggleButton variant= "accent" id="v2" value="v2" onClick={() =>this.props.switchVersion('v2')}>V2</ToggleButton> 
                        </ToggleButtonGroup>
                        :
                        ''
                    }

                    <Button size="sm" variant="fuchsia" className="text-dark" onClick={() =>this.props.changeView('governancetoken')}>{this.props.outputNumber(parseFloat(this.props.GovernanceTokenTotalBalance),2)} IPT</Button>
                    <div>&nbsp;</div>
                    <Button size="sm" variant="light" className="text-dark" onClick={() =>this.props.showAccount()}>{this.props.addressShort}</Button>                   
                </div>
            </nav>
            <div className="row" style={{height: 20}}></div>    
        </div>
    )
  } 
}

export default Navbar;
