async switchVersion(version) {
  if (version == "v1") {
    console.log("Switch to V1")
    await this.setState({version:'v1'})
    await this.loadWeb3();
    await this.loadBlockchainData();

  }
  else if (version == "v2") {
    console.log("Switch to V2")
    await this.setState({version:'v2'})
    await this.loadWeb3();
    await this.loadBlockchainData();

  }
}



